.video__block {
	z-index: 1;
	position: relative;
	height: 815px;
	background: #fff;
	@include r(768) {
		height: 500px; }
	.grad {
		position: absolute;
		top: -300px;
		right: 0;
		left: 0;
		width 100% {}
		height: 300px;
		background: transparent;
		@include rmin(768) {
			@include g(transparent ,#fff); } }
	.title {
		padding: 130px 0 105px 35px;
		color: black;
		@include f(l,36);
		text-align: center;
		position: relative;
		z-index: 1;
		@include r(768) {
			padding: 84px 0 45px; } }



	img {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translate(-50%,0);
		width: auto;
		height: 100%; }
	#play {
		position: absolute;
		top: 64%;
		left: 50%;
		transform: translate(-50%, 0); }
	.block-text {} }

