body {
	font-family: 'OpenSans', Arial, sans-serif;
	font-size: 15px;
	@include font-smoothing;
	background: grey;
	color: #FFF; }
//variables
* {
	@include box; }

$orange: #ee931e;
$blue-light: #479df3;
$blue-dark: #147de7;
$menu-back: #051523;
$red: #fcead1;
$color-dark: #383838;

$orange-btn: #ffb300;
$blue-btn: #479df3;
$footer-dark: #323232;
$footer-light: #676767;
$social: #f7c042;
$footer-grey: #979797;

%in {
	width: 1170px;
	//width: 100%
	margin: 0 auto;
	max-width: 1170px;
	@include r(1024) {
		width: 100%; } }


.out {
	overflow: hidden;
	color: black; }

h2 {
	line-height: 1.1;
	@include f(l,36);
	@include r(414) {
		font-size: 35px; }
	@include r(375) {
		font-size: 35px; } }


h3 {
	@include f(l,16,1);
	@include r(414) {
		font-size: 18px; }
	@include r(375) {
		font-size: 16px; }
	@include r(320) {
		font-size: 12px; } }


// +r(767)
