// 		font-size: 32px
// 	.h2
// 		font-size: 32px

.svg-icon {
	display: inline-block;
	width: 34px;
	height: 34px;
	fill: transparent;
	margin-left: 5px;
	svg {
		display: block;
		width: 100%;
		height: 100%; } }

.svg-subscribe {
	fill: #f5a623;
	width: 45px;
	height: 45px; }

.btn {
	display: inline-block;
	background: none;
	box-sizing: border-box;
	text-transform: uppercase;
	text-align: center;
	position: relative;
	z-index: 2;
	@include middle;
	background: transparent;
	margin-right: 0;
	text-decoration: none;
	overflow: hidden;
	border-radius: 100px;
	@include f(semi, 16);
	@include tr(opacity .5s);
	font-size: 0;
	white-space: nowrap;

	line-height: 48px;
	height: 48px;
	.btn__text {
		padding: 0px 18px 0px 13px;
		//font-size: 16px
		color: #fff;
		font-size: 16px; }

	.btn__inner {
		font-size: 0;
		line-height: 0px;
		border: 3px solid transparent;
		display: inline-block;
		vertical-align: middle;
		overflow: hidden;
		& > * {
			@include middle; } }
	&:hover {
		opacity: .8; } }

.btn__text {
	position: relative;
	z-index: 1; }

.btn_orange {
	background-color: $orange-btn; }
.btn_blue {
	background-color: $blue-btn; }

.btn_big {
	height: 105px;
	width: 105px; }
